@value Variables: "../../../../styles/Variables.css";
@value typeform-border-color, small-gutter, thin-gutter from Variables;


.controlCont{
  position: absolute;
  right: -18px;
  top: 28px;
  z-index: 2000;
}

.removeElem,.configElem{
  float:left;
  cursor: pointer;
  border:1px solid #eee;
  padding:2px 8px 0px;
  border:1px solid #eee;
  box-shadow:inset 0px 0px 3px var(--color1);
  box-shadow:0px 0px 3px #ccc;
  border-radius: 5px;
  z-index: 999;
  background:#fff;
}
.removeElem:hover,.configElem:hover{
  box-shadow:0px 0px 10px #ccc;
}
.removeElem:active,.configElem:active{
  box-shadow:inset 0px 0px 3px #ccc;
}
.configElem{
  margin-right:15px;
}
.removeElem {
 
  color: red;
 
}

.clearfix{
  clear: both;
}