*,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

:root {
  --color1: #ccc;
  --color2: #ccc;
  --color3: #ccc;
  --color4: #ccc;
  --color5: #ccc;
  --color6: #ccc;
  --color7: #ccc;
  --color8: #ccc;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

label {
  cursor: default;
}

#root {
  height: 100vh;
}

.ActivteElement {
  width: 100%;
  opacity: 1;
  position: relative;
  -webkit-animation: none 0s ease 0s 1 normal none running;
          animation: none 0s ease 0s 1 normal none running;
}

.InactiveElement {
  width: 100%;
  opacity: 0.2;
  position: relative;
  -webkit-animation: none 0s ease 0s 1 normal none running;
          animation: none 0s ease 0s 1 normal none running;
}

