  text-align: center;
}

.heading-tertairy {
  font-size: 1.6rem;
}


.btn {
  text-decoration: none;
  font-size: 1.6rem;
  padding: 0.6rem 2rem;
  display: inline-block;
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
  cursor: pointer;
  outline: none;
}

.btn--primary {
  background-color: #EB1761;
  color: #fff;
}

.btn--action {
  white-space: nowrap;
  border-radius: 10rem;
}

.btn--action:hover {
  opacity: 0.8;
}

.btn--navigation {
  padding: 0.8rem 1.2rem;
  margin-top: 1rem;
}

.btn--navigation:first-child {
  margin-right: 0.5rem;
}



